namespace $ { $mol_style_attach( "mpk/tss/card/card.css",
 "[mpk_tss_card] {\n\tpadding: 0.5rem;\n\tdisplay: block;\n\tflex: 0 1 auto;\n\tposition: relative;\n\tmin-height: 110px;\n}\n\n[mpk_tss_card_content] {\n\tpadding: 0;\n}\n\n[mpk_tss_card_status] {\n\tbackground: var(--mol_theme_line);\n\tcolor: var(--mol_theme_text);\n\tpadding: 0;\n\tline-height: 2;\n\tmargin: 0;\n\tdisplay: flex;\n\tjustify-content: space-between;\n}\n\n[mpk_tss_card_status_text] {\n\tpadding: 0 0.5rem;\n}\n\n[mpk_tss_card] > [mol_card_status_type=\"success\"] {\n\t--mol_theme_line: var(--mpk_theme_status_bg_success);\n\t--mol_theme_control: var(--mpk_theme_status_control);\n\t--mol_theme_text: var(--mpk_theme_status_text);\n}\n\n[mpk_tss_card] > [mol_card_status_type=\"warning\"] {\n\t--mol_theme_line: var(--mpk_theme_status_bg_warning);\n\t--mol_theme_control: var(--mpk_theme_status_control);\n\t--mol_theme_text: var(--mpk_theme_status_text);\n}\n[mpk_tss_card] > [mol_card_status_type=\"ready\"] {\n\t--mol_theme_line: var(--mpk_theme_status_bg_success);\n\t--mol_theme_control: var(--mpk_theme_status_control);\n\t--mol_theme_text: var(--mpk_theme_status_text);\n}\n\n[mpk_tss_card] > [mol_card_status_type=\"not_responding\"],\n[mpk_tss_card] > [mol_card_status_type=\"error\"] {\n\t--mol_theme_line: var(--mpk_theme_status_bg_error);\n\t--mol_theme_control: var(--mol_theme_field);\n\t--mol_theme_text: var(--mpk_theme_status_text);\n}\n\n[mpk_tss_card] > [mol_card_status_type=\"inactive\"] {\n\t--mol_theme_line: var(--mpk_theme_status_bg_inactive);\n\t--mol_theme_control: var(--mol_theme_field);\n\t--mol_theme_text: var(--mpk_theme_status_text);\n}\n\n[mpk_tss_card] > [mol_card_status_type=\"unknown\"] {\n\t--mol_theme_line: var(--mpk_theme_status_bg_unknown);\n\t--mol_theme_control: var(--mol_theme_field);\n\t--mol_theme_text: var(--mpk_theme_status_text);\n}\n"
) }